				'#ffb980',
				'#858fa8',
				'#b6a2de',
				'#dcc70c',
				'#2bc7c8',
				// '#f3e4b4',
				// 'black',
			][rindex()];


			myChart666.showLoading();

			$.get(
				'http://124.223.32.208:8000/media/json_files/price_i.json',
				function(json) {
					option = {
						animationDurationUpdate: 1500,
						animationEasingUpdate: 'bounceIn',
						series: [{
							layout: 'force',
							force: {
								repulsion: 300,
								edgeLength: [150, 130],
								layoutAnimation: true
							},
							legendHoverLink: true,
							animation: true,
							draggable: true,
							type: 'graph',
							roam: "true",
							// progressiveThreshold: 700,
							data: json.nodes
								.filter((item) => item.size > 15)
								.map(function(node) {
									let color = mycolor();
									let bgcolor = backgroundColor();
									return {
										id: node.id,
										name: node.label,
										value: node.value,
										symbolSize: node.size / 1.0,
										itemStyle: {
											color: bgcolor,
											borderColor: color,
											borderWidth: node.size / 30
										},
										label: {
											show: true,
											fontSize: node.size / 5,
											// color: '"#5b5b5b"',
											// color: '"#fff"',
											formatter: function(param) { // 标签内容
												return param.data.name + "\n" + (param.data
													.symbolSize *
													1.5).toFixed(2);
											},
										}
									};
								}),
							zoom: 1,
							center: ['50%', '50%'],
							roam: true,
							edgeSymbol: ['circle', 'arrow']
						}]
					};
					myChart666.hideLoading();
					myChart666.setOption(
						option
					);
					// console.log(option.series[0].data)
					// setInterval(function() {
					// 	let random = (Math.random() * 100).toFixed(1) - 0;
					// 	option.series[0].data[0].value = random;
					// 	option.series[0].data[1].value = random;
					// 	myChart666.setOption(option)

					// }, 10)
				},
			);



			option && myChart666.setOption(option);

