									lineStyle: {
										type: 'solid',
										color: "#5b5b5b",
										width: '1  ', //坐标线的宽度
									}
								},
							},
							yAxis: {
								type: 'category',
								axisLabel: {
									textStyle: {
										show: true,
										fontFamily: '微软雅黑',
										color: "#5b5b5b",
										fontSize: fontSize(0.18),
									},
								},
								axisLine: {
									lineStyle: {
										type: 'solid',
										color: "#5b5b5b",
										width: '1  ', //坐标线的宽度
									}
								},
							},
							// visualMap: {
							// 	orient: 'horizontal',
							// 	left: 'center',
							// 	min: 10,
							// 	max: 100,
							// 	text: ['High Score', 'Low Score'],
							// 	// Map the score column to color
							// 	dimension: 0,
							// 	inRange: {
							// 		color: ['#65B581', '#FFCE34', '#FD665F']
							// 	}
							// },
							series: [{
								type: 'bar',
								label: {
									show: true,
									fontSize: fontSize(0.17),
								},
								color: "#309bd7",
                                fontSize: fontSize(0.16),
								encode: {
									// Map the "amount" column to X axis.
									x: 'amount',
									// Map the "product" column to Y axis
									y: 'product',
                                    fontSize: fontSize(0.18),
								},
								itemStyle: {
									barBorderRadius: 5,
									borderWidth: 1,
                                    fontSize: fontSize(0.16),
									borderType: 'solid',
									borderColor: '#65B581',
									shadowColor: '#5470c6',
									shadowBlur: 3
								}
							}]

						}
					);

				});
		},

    }
}
</script>

